import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Button from 'react-bootstrap/Button';
import logo from '../../Media/RWH-LOGO-FINVWEB3.png'

const NavBar = ({ onRouteChange, isSignedIn }) => {
	return(
		<Navbar style={{backgroundColor: '#1890ff'}} variant="dark">		    
			<Navbar.Brand href="https://realworld.health/">
				<img
					alt=""
					src={logo}
					height="23"
					className="d-inline-block align-top"
					style={{marginLeft: '20px', marginRight: '10px'}}
				/>{' '}
					Web Developer Assessment
			</Navbar.Brand>
			{
				isSignedIn
					? (
						<div className='ms-auto' style={{marginRight: '20px'}}>
							<Navbar.Toggle aria-controls="basic-navbar-nav" />
							<Navbar.Collapse id="basic-navbar-nav">
								<Nav>
									<NavDropdown title={<i className="bi bi-person"></i>} id="basic-nav-dropdown">
										<Button
											style={{backgroundColor: '#1890ff', borderColor: 'white'}} 
											className="btn ms-auto button-color" 
											type="button" 
											onClick={() => onRouteChange('passwordreset')} 
										>
											 Password Reset
										</Button>										
									</NavDropdown>
									<Button
										style={{backgroundColor: '#1890ff', borderColor: 'white', marginLeft: '15px'}} 
										className="btn ms-auto button-color" 
										type="button" 
										onClick={() => onRouteChange('signin')} 
									>
										Log Out
									</Button>
								</Nav>
							</Navbar.Collapse>							
						</div>
						)
					: (
						<Button
							style={{backgroundColor: '#1890ff', borderColor: 'white', margin: '0 20px'}} 
							className="btn ms-auto" 
							type="button" 
							onClick={() => onRouteChange('signin')} 
						>
							Log In
						</Button>
						)
			}
			
		</Navbar>
	);
}

export default NavBar;