import React, { useState } from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import side_picture from '../../Media/side_picture.png';

function ForgotPassword({ onRouteChange }) {
	const [userEmail, setUserEmail] = useState('');	

   const emailValidation = (e) => {
      if (typeof userEmail !== "undefined") {
         let lastAtPos = userEmail.lastIndexOf("@");
         let lastDotPos = userEmail.lastIndexOf(".");

         if (
            !(
               lastAtPos < lastDotPos &&
               lastAtPos > 0 &&
               userEmail.indexOf("@@") === -1 &&
               lastDotPos > 2 &&
               userEmail.length - lastDotPos > 2
            )
            ) {
            //Email NOT valid
            return(false);
         } else {
         	return(true)
         }
      }
   }

   const formValidation = () => {
   	if ( !emailValidation() ) {
   		document.getElementById('emailForm').style.border = '1px solid red';
   		return(false);
   	} else {
      	document.getElementById('emailForm').style.border = '1px solid #ced4da';
      	return(true);
      }
   }  

   const onLoginBtnClick = () => {
   	if (formValidation()) {
   		fetch('https://86.2.58.131:3003/passwordReset', {
				method: 'put',
				headers: {'Content-Type': 'application/json'},
				body: JSON.stringify({
					userEmail: userEmail
				})
			})
				.then(response => response.json())
				.then(data => {
					if (data === 'Success') {
						onRouteChange('signin');
					} else {
						document.getElementById('err').innerHTML = data;
					}
				})
   	}
   }


	return(
		<Container fluid>
			<Row>
				<Col xs={12} md={7} >
					<img src={side_picture} width='99%' alt='side_picture' />
				</Col>
				<Col xs={12} md={4}>
					<h3 style={{margin: '25px 0'}}>Forgot Password</h3>
					<Form>
						<Form.Group className="mb-3" controlId="emailForm">
							<Form.Label>Email address</Form.Label>
							<Form.Control type="email" placeholder="Email address" onChange={(event) => setUserEmail(event.target.value)} />
						</Form.Group>
						<Button style={{backgroundColor: '#1890ff'}} type="button" onClick={onLoginBtnClick} >
							Reset
						</Button>
						<p style={{color: 'red'}} id='err'></p>
					</Form>
				</Col>
			</Row>
		</Container>
	);
}

export default ForgotPassword;