import React, { useState } from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import side_picture from '../../Media/side_picture.png';

function PasswordReset({ onRouteChange, user }) {
	const [userPasswordOld, setUserPasswordOld] = useState('');
	const [userPasswordNew, setUserPasswordNew] = useState('');

	const userPasswordValidation = () => {
   	const pattern = /^(?=.*[A-Za-z])(?=.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{10,}$/;
   	if (!pattern.test(userPasswordNew)) {
   		return(false)
   	} else {
   		return(true)
   	}
   }

   const formValidation = () => {
   	if ( !userPasswordOld || !userPasswordValidation() ) {
         if (!userPasswordOld) {
            document.getElementById('passwordOldForm').style.border = '1px solid red';
         } else {
         	document.getElementById('passwordOldForm').style.border = '1px solid #ced4da';
         }

         if (!userPasswordValidation() ) {
            document.getElementById('passwordNewForm').style.border = '1px solid red';
         } else {
         	document.getElementById('passwordNewForm').style.border = '1px solid #ced4da';
         }
         return(false);
      } else {
      	document.getElementById('passwordOldForm').style.border = '1px solid #ced4da';
      	document.getElementById('passwordNewForm').style.border = '1px solid #ced4da';
         return(true);
      }
   }
   

   const onResetBtnClick = () => {
   	if (formValidation()) {
   		fetch('https://86.2.58.131:3003/newPassword', {
				method: 'put',
				headers: {'Content-Type': 'application/json'},
				body: JSON.stringify({
					userName: user.name,
					userPasswordOld: userPasswordOld,
					userPasswordNew: userPasswordNew
				})
			})
				.then(response => response.json())
				.then(data => {
					if (data === 'Success') {
						onRouteChange('signin');
					} else {
						document.getElementById('err').innerHTML = data;
					}
				})
   	}
   }


	return(
		<Container fluid>
			<Row>
				<Col xs={12} md={7} >
					<img src={side_picture} width='99%' alt='side_picture' />
				</Col>
				<Col xs={12} md={4}>
					<h3 style={{margin: '25px 0'}}>Password Reset</h3>
					<Form>
						<Form.Group className="mb-3" controlId="passwordOldForm">
							<Form.Label>Old Password</Form.Label>
							<Form.Control type="password" placeholder="Password" onChange={(event) => setUserPasswordOld(event.target.value)} />
						</Form.Group>
						<Form.Group className="mb-3" controlId="passwordNewForm">
							<Form.Label>New Password</Form.Label>
							<Form.Control type="password" placeholder="Password" onChange={(event) => setUserPasswordNew(event.target.value)} />
						</Form.Group>
						<Button style={{backgroundColor: '#1890ff'}} type="button" onClick={onResetBtnClick} >
							Reset
						</Button>
						<p style={{color: 'red'}} id='err'></p>
					</Form>
				</Col>
			</Row>
		</Container>
	);
}

export default PasswordReset;