import React, { useState, useEffect } from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import side_picture from '../Media/side_picture.png';

function Register({ onRouteChange, onSignIn }) {
	const [userName, setUserName] = useState('');
   const [userEmail, setUserEmail] = useState('');
   const [userPassword, setUserPassword] = useState('');

   const emailValidation = (e) => {
      if (typeof userEmail !== "undefined") {
         let lastAtPos = userEmail.lastIndexOf("@");
         let lastDotPos = userEmail.lastIndexOf(".");

         if (
            !(
               lastAtPos < lastDotPos &&
               lastAtPos > 0 &&
               userEmail.indexOf("@@") === -1 &&
               lastDotPos > 2 &&
               userEmail.length - lastDotPos > 2
            )
            ) {
            //Email NOT valid
            return(false);
         } else {
         	return(true)
         }
      }
   }

   const userPasswordValidation = () => {
   	const pattern = /^(?=.*[A-Za-z])(?=.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{10,}$/;
   	if (!pattern.test(userPassword)) {
   		return(false)
   	} else {
   		return(true)
   	}
   }

   const formValidation = () => {
   	if ( !userName || !emailValidation() || !userPasswordValidation() ) {
         if (!userName) {
            document.getElementById('userNameForm').style.border = '1px solid red';
         } else {
         	document.getElementById('userNameForm').style.border = '1px solid #ced4da';
         }

         if (!emailValidation() ) {
         	document.getElementById('emailForm').style.border = '1px solid red';
         } else {
         	document.getElementById('emailForm').style.border = '1px solid #ced4da';
         }

         if (!userPasswordValidation() ) {
            document.getElementById('passwordForm').style.border = '1px solid red';
            document.getElementById('errPass').innerHTML = 'Password MUST include at least: 1 capital letter, 1 special character and be minimum 10 characters long';

         } else {
         	document.getElementById('passwordForm').style.border = '1px solid #ced4da';
         	document.getElementById('errPass').innerHTML = '';

         }
         return(false);
      } else {
      	document.getElementById('userNameForm').style.border = '1px solid #ced4da';
      	document.getElementById('emailForm').style.border = '1px solid #ced4da';
      	document.getElementById('passwordForm').style.border = '1px solid #ced4da';
         return(true);
      }
   }  

   const onRegisterBtnClick = () => {
   	if (formValidation()) {
   		document.getElementById('registerbtn').innerHTML = 'Registering...';
   		alert('Please check your email');
   		fetch('https://86.2.58.131:3003/register', {
				method: 'post',
				headers: {'Content-Type': 'application/json'},
				body: JSON.stringify({
					userName: userName,
					userEmail: userEmail,
					userPassword: userPassword
				})
			})
				.then(response => response.json())
				.then(data => {
					if (data === 'Success') {
						onRouteChange('signin');
					} else {
						document.getElementById('registerbtn').innerHTML = 'Register';
						document.getElementById('err').innerHTML = data;
					}
				})
   	}
   }

   useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        console.log("Enter key was pressed. Run your function.");
        event.preventDefault();
        onRegisterBtnClick();
      }
    };
    document.addEventListener("keydown", listener);
    return () => {
      document.removeEventListener("keydown", listener);
    };
  });

	return(
		<Container fluid>
			<Row>
				<Col xs={12} md={7} >
					<img src={side_picture} width='99%' alt='side_picture' />
				</Col>
				<Col xs={12} md={4}>
					<h3 style={{margin: '25px 0'}}>Register a new user</h3>
					<Form>
						<Form.Group className="mb-3" controlId="userNameForm">
							<Form.Label>User Name</Form.Label>
							<Form.Control type="text" placeholder="User Name" onChange={(event) => setUserName(event.target.value)} />
						</Form.Group>
						<Form.Group className="mb-3" controlId="emailForm">
							<Form.Label>Email address</Form.Label>
							<Form.Control type="email" placeholder="Email address" onChange={(event) => setUserEmail(event.target.value)} />
						</Form.Group>
						<Form.Group className="mb-3" controlId="passwordForm">
							<Form.Label>Password</Form.Label>
							<Form.Control type="password" placeholder="Password" onChange={(event) => setUserPassword(event.target.value)} />
						</Form.Group>
						<Button id='registerbtn' style={{backgroundColor: '#1890ff'}} className='button-color' type="button" onClick={onRegisterBtnClick} >
							Register
						</Button>
						<p style={{color: 'red'}} id='err'></p>
					</Form>
					<div className="lh-copy mt3">
						<p style={{color: '#1cc792', margin: '15px 0'}} onClick={() => onRouteChange('signin')}>Log in</p>
					</div>
				</Col>
			</Row>
		</Container>
	);
}

export default Register;