import React, { useState, useEffect} from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import side_picture from '../Media/side_picture.png';

function SignIn({ onRouteChange, onSignIn, loadUser }) {
	const [userName, setUserName] = useState('');
   const [userPassword, setUserPassword] = useState('');

   const formValidation = () => {
   	if ( !userName || !userPassword ) {
         if (!userName) {
            document.getElementById('userNameForm').style.border = '1px solid red';
         } else {
         	document.getElementById('userNameForm').style.border = '1px solid #ced4da';
         }

         if (!userPassword) {
            document.getElementById('passwordForm').style.border = '1px solid red';
         } else {
         	document.getElementById('passwordForm').style.border = '1px solid #ced4da';
         }
         return(false);
      } else {
      	document.getElementById('userNameForm').style.border = '1px solid #ced4da';
      	document.getElementById('passwordForm').style.border = '1px solid #ced4da';
         return(true);
      }
   }

   const onLoginBtnClick = () => {
   	if (formValidation()) {
   		document.getElementById('loginbtn').innerHTML = 'Loging...';
   		fetch('https://86.2.58.131:3003/signin', {
				method: 'post',
				headers: {'Content-Type': 'application/json'},
				body: JSON.stringify({
					userName: userName,
					userPassword: userPassword
				})
			})
				.then(response => response.json())
				.then(data => {
					if (data === 'Success') {						
						onRouteChange('home');
						onSignIn(true);
						loadUser(userName);
					} else {
						document.getElementById('loginbtn').innerHTML = 'Log in';
						document.getElementById('err').innerHTML = data;
					}
				})
	   }
   }

   useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        console.log("Enter key was pressed. Run your function.");
        event.preventDefault();
        onLoginBtnClick();
      }
    };
    document.addEventListener("keydown", listener);
    return () => {
      document.removeEventListener("keydown", listener);
    };
  });


	return(
		<Container fluid>
			<Row>
				<Col xs={12} md={7} >
					<img src={side_picture} width='99%' alt='side_picture' />
				</Col>
				<Col xs={12} md={4}>
					<h3 style={{margin: '25px 0'}}>Log in</h3>
					<Form>
						<Form.Group className="mb-3" controlId="userNameForm">
							<Form.Label>User Name</Form.Label>
							<Form.Control type="text" placeholder="User Name" onChange={(event) => setUserName(event.target.value)} />
						</Form.Group>
						<Form.Group className="mb-3" controlId="passwordForm">
							<Form.Label>Password</Form.Label>
							<Form.Control type="password" placeholder="Password" onChange={(event) => setUserPassword(event.target.value)} />
							<p style={{color: '#1cc792'}} onClick={() => onRouteChange('forgotpassword')}>Forgot Password?</p>
						</Form.Group>
						<p style={{color: 'red'}} id='err'></p>
						<Button id='loginbtn' style={{backgroundColor: '#1890ff'}}  type="button" onClick={onLoginBtnClick} >
							Log in
						</Button>
					</Form>
					<div className="lh-copy mt3">
						<p style={{color: '#1cc792', margin: '15px 0'}} onClick={() => onRouteChange('register')}>Or Register a new user</p>
					</div>
				</Col>
			</Row>
		</Container>
	);
}

export default SignIn;